#contact-form {
  filter: drop-shadow(2px 4px 6px black);
  background-color: rgba(152, 151, 151, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-top: 60px;
  margin-bottom: 50px;
  border: 1px solid white;
  border-radius: 10px;
  padding: 40px;
}

#contact-form-header {
  font-size: 40px;
  color: white;
}

#form-slot-1 {
  font-size: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#form-slot-1 input {
  border-radius: 5px;
}

#form-slot-2 {
  font-size: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#form-slot-2 label {
  color: white;
  padding-bottom: 10px;
}

#form-slot-2 input {
  border-radius: 5px;
}

#form-slot-3 {
  font-size: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#form-slot-1 label {
  color: white;
  padding-bottom: 10px;
}

#form-slot-3 label {
  color: white;
  padding-bottom: 10px;
}

#form-slot-3 textarea {
  border-radius: 5px;
  font-size: 12px;
}

.contact-submit-btn {
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  padding: 10px;
  color: white;
  font-weight: bold;
  font-size: 15px;
}

.contact-submit-btn:hover {
  color: silver;
  border: 1px solid silver;
  transition: 0.5s ease;
  cursor: pointer;
}

.contact-info-line {
  display: flex;
  align-items: center;
  color: white;
  font-size: 20px;
}

.contact-info-line p {
  padding-left: 10px;
}

@media only screen and (min-width: 320px) and (max-width: 479px) {

  #contact-form {
    width: 200px;
    margin: 25px;
  }

  #form-slot-3 textarea {
    width: 100%;
  }

}

@media only screen and (min-width: 480px) and (max-width: 820px) {

    #contact-form {
        width: 200px;
        margin: 25px;
      }
    
      #form-slot-3 textarea {
        width: 100%;
      }
}


